@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
.result {
    width : 30vw;
    font-family: 'Montserrat',sans-serif;
    margin-left : 35vw;
    text-align: center;
    position: relative;
    height : 75vh;
    background-color: white;
    border : solid 1px grey;
    box-shadow: 4px 4px rgba($color: #333333, $alpha: 0.5);
    margin-top: 5vh;
    h1 {
        max-width: 30vw;
        font-family: 'Montserrat',sans-serif;
        color : #0059fd;
    }
    h2 {
        max-width : 30vw;
        font-family: 'Montserrat',sans-serif;
    }
    will-change : opacity , top;
    animation: appear .75s;
}

@media screen and (orientation : portrait){
    .result {
        width : 75vw;
        margin-left : 12.5vw;
        h1,h2 {
            max-width: 75vw;
        }
    }
}

@keyframes appear {
    from {
        opacity : 0;
        top : 35px;
    }
    to {
        opacity : 1;
        top : 0;
    }
}