.App {
  text-align: center;
}

*{
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body { 
  background-image: url('https://i0.wp.com/www.anupghosal.com/wp-content/uploads/2020/06/cool-nature-backgrounds-68-images-cool-backgrounds-hd.jpg?fit=2194%2C1458&ssl=1&is-pending-load=1');
  background-repeat : no-repeat;
  overflow: hidden;
  height : 100vh;
  background-size: cover;
}

@media screen and (orientation : landscape){
  body {
    background-image: url('https://i0.wp.com/www.anupghosal.com/wp-content/uploads/2020/06/cool-nature-backgrounds-68-images-cool-backgrounds-hd.jpg?fit=2194%2C1458&ssl=1&is-pending-load=1');
    background-size: cover;
    background-repeat : no-repeat;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
