@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
.Form {
    display: flex;
    justify-content: center;
    padding-top: 2.5vh;
    font-family: 'Montserrat',sans-serif;
    z-index: 999;
    position: relative;
    top : 0;
    transition: .6s;

    input {
        width : 30vw;
        height : 5vh;
        font-size: larger;
        font-family: 'Montserrat',sans-serif;
        text-align: center;
        border : none;
        background-color: transparent;
        color : white;
        border-bottom : white solid 0.75vh;
        outline: none;
    }
    input::placeholder{
        color : white;
        position: relative;
        transition: 0.5s;
    }
    input:focus::placeholder {
        font-size: large;
    }
  

    button {
        font-size: larger;
        font-family: 'Montserrat',sans-serif;
        background-color: transparent;
        border: solid 3px #0059fd;
        color : #0059fd;
        will-change: color;
        transition: .5s;
        position: relative;
        z-index: 100;
        box-sizing: border-box;
        margin-left : 1vw;
        outline: none;
    }
    button:hover {
        color : white;
    }

    button::before {
        content: '';
        top : 0;
        left : 0;
        width : 100%;
        height : 100%;
        background-color: #0059fd;
        position: absolute;
        z-index: -1;
        transform: scaleX(0);
        transition: .75s;
        will-change: transform;
        transform-origin: 0 0;
    }
    button:hover::before {
        transform: scaleX(1);
    }

}
.Form2 {
    @extend .Form;
    padding-top : 25vh;
    top : 5vh;
    transform: scale(1.25);
}
@media screen and (orientation : portrait){
    .Form {
        input {
            width : 50vw;
        }
    }
}
p {
    @extend .Form;
    text-align: center;
}

.title {
    font-family: 'Montserrat',sans-serif;
    text-align: center;
    color : white;
    transform: scale(1.5);
    margin-top: 10vh;
    position: relative;
    animation: appear .75s;
    will-change: opacity top;
}

@keyframes appear {
    from {
        opacity : 0;
        top : 35px;
    }
    to {
        opacity: 1;
        top : 0;
    }
}
